import React, { Component } from 'react'
import './Microblading.css'

class Microblading extends Component {

    render()
    {
        return(
            <div classname="white">
                <div>
                    <h1>Was ist Microblading?</h1>
                    <p>Microblading ist eine aus Asien stammende Technik bei der im Gegensatz zum herkömmlichen Permanent-Make-up keine Maschine zum Einsatz kommt, sondern die Farbe manuell in die Haut eingebracht wird.  Konkret besteht der Pen aus aneinandergereihten „Klingen“, den sogenannten „Blades“. Mit diesen Blades werden filigrane Härchen in die Haut geritzt und die Farbpigmente werden somit in die Haut eingearbeitet Die Haltbarkeit beträgt bei den Augenbrauen je nach Hautbeschaffenheit und diversen anderen Faktoren wie beispielsweise Stoffwechsel etwa neun Monate bis zwei Jahre.</p>
                    <br></br>
                    <p className = "silver">Es gibt ganz unterschiedliche Techniken, so dass ganz individuelle Looks entstehen. Auf diese Weise lassen sich Augenbrauen optisch verdichten. Auf Wunsch kann auch eine ganz andere Form entstehen. Es ist erstaunlich, wie sich der Gesichtsausdruck dadurch positiv verändern lässt.</p>
                    <h2>Vorher: </h2>
                    <p>1-2 Tage vorher ein sanftes Peeling auf die Augenbrauenparie auftragen, um abgestorbene Hautschüppchen zu entfernen und um die Haut zu glätten und erfrischen.
Trage einen Tag vor der Behandlung bitte keine ölhaltigen Produkte mehr auf die Augenbrauen auf.
Am Behandlungstag keine koffeeinhaltigen Getränke (Kaffee, Energie Gertänke, schwarzen Tee ect.) trinken, es verstärkt den Blutkreislauf
Keine Bluterdünnenden Medikamente (Asperin,Macumar……usw.) einnehmen. Kein Alkohol- und Drogenkosnum
Botox- oder Hyaluronbehandlungen sollten 4 Wochen her sein
1 Woche vorher auf  Solariumbesuch und starke Sonneneinstrahlung verzichten

</p>
                <h3>Nachher:</h3>
                <p>In den ersten 24 Std. die Behandelte Stelle mit einem feuchten Wattepad am besten abgekochtem Wasser tupfend säubern. Wiederholen sie dies alle 2-3 Std. Tragen sie danach dünn die mitgegebene Pflegecreme auf.
Ab dem zweiten Tag die Augenbrauen morgens/abends dünn mit der Pflegecreme und einem sauberen Finger eincremen. 
Zwischen dem 3-5 Tag kann sich eine Pigmentierkruste bilden, bitte nicht pullen man könnte das Pigment mitrausziehen. Die Kruste muss von selbst abfallen!
<br></br>
Bitte berühren Sie die frisch Pigmentierung (nur wenn unbedingt nötig) sehr vorsichtig und mit sauberen Händen damit keine Bakterien in die Wunde gelangen.
</p>
                </div>
            </div>
        )
    }
}

export default Microblading